import { Component, OnDestroy, OnInit } from '@angular/core';
import { Label, ActionLabel, Days } from "@enum/app_enum";
import { HomeService } from "@core/services/serviceIndex";
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-admin-home',
  templateUrl: './admin-home.component.html',
  styleUrls: ['./admin-home.component.css']
})
export class AdminHomeComponent implements OnInit, OnDestroy {
  public tabLists: any = [ActionLabel.teacherTimeline, ActionLabel.paymentInfo];
  public activeIndex: any;
  public paymentLists: any = {};
  public tableHeader = [Label.payDate, Label.studentName, Label.className, Label.paymonths, Label.amount];
  public actionBtns = [];
  public objectLabel: any = ["payDate", "studentName", "className", "duration", "amount"];
  public filter: any = {
    sortColumn: this.objectLabel[0],
    sortDirection: "asc",
    index: 0
  };
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public searchData: any;
  public isLoading: boolean = false;
  public totalCount: any;
  public subscription: Subscription;

  public timetableData:any;

  
  constructor(private _service: HomeService) { }

  ngOnInit(): void {


  }


  tabChange(index: any) {
    this.isLoading = true;
    this.activeIndex = index;
    if (this.activeIndex == 1)
      this.getPaymentLists();
  }

  getPaymentLists() {
    this.subscription = this._service.getPaymentList(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection)
      .subscribe((res) => {
        this.paymentLists.data = res.paymentInfo;
        this.totalCount = res.totalCount;
        this.paymentLists.tableHeader = this.tableHeader;
        this.paymentLists.actionBtns = this.actionBtns;
        this.paymentLists.objectLabel = ["payDate", "studentName", "className", "duration", "amount"];
        this.isLoading = false;
      });
  }


  pageChanges(e: any) {
    this.page = e;
    this.getPaymentLists();
  }

  onFilter(filter: {}) {
    this.filter = filter;
    this.getPaymentLists();
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
